html,body {
  height: 100%;
}
#root {
  height: 100%;
}

.timers {
  min-height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.timer {
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  text-align: center;
  font-size: 40px;
  font-weight: bold;

  opacity: 0.3;
  color: white;
  background: rgb(242, 111, 33); /* orange */
  -webkit-transition: opacity 0.5s;
  -o-transition: opacity 0.5s;
  transition: opacity 0.5s;
}

.timer--active {
  opacity: 1;
}

.timer--top {
  -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
          transform: rotate(180deg);
}



